import classnames from 'classnames';
import {
  publicUrl,
} from '../../publicUrl';
import {
  Link as ReactRouterLink,
} from 'react-router-dom';

import * as React from 'react';

import styles from './index.module.scss';

export const Link = ({
  children,
  className,
  props,
  to,
}) => {
  let fullTo = publicUrl;
  if (to) {
    if (to[0] === '/') {
      fullTo += to.slice(1);
    } else {
      fullTo += to;
    }
  }

  return (
    <ReactRouterLink
      {...props}
      className={classnames(styles.link, className)}
      to={fullTo}
    >{
      children
    }</ReactRouterLink>
  );
};
