import {
  publicUrl,
} from '../../publicUrl';
import {
  Route as ReactRouterRoute,
} from 'react-router-dom';

import * as React from 'react';

export const Route = ({
  path,
  ...props,
}) => (
  <ReactRouterRoute
    {...props}
    path={`${publicUrl.slice(0, -1)}${path && path.endsWith('/') ? path.slice(0, -1) : path}`}
  />
);
